.page-footer {
  margin: 0 0 74px 0;
  padding: 0 50px;

  @media (max-width: $max-tablet-width) {
    padding: 0 21px;
  }

  @media (max-width: $max-mobile-width) {
    padding: 0 16px;
  }
}

.page-footer__group {
  display: flex;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0;

  @media (max-width: $max-tablet-width) {
    flex-wrap: wrap;
    row-gap: 16px;
  }

  @media (max-width: $max-mobile-width) {
    row-gap:  unset;
  }
}
