
.burger-open {
  display: none;
  @media (max-width: $max-tablet-width) {
    display: block;
    position: absolute;
    top: 36px;
    right: 0;
    width: 320px;
    height: 568px;
    transition: 1s;
    background-color: $page-white-color;
  }
}

.burger-open__menu {
  width: 50px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 0 0 82px auto;
  padding: 0 20px 0 0;
}

.burger-open__menu-inner {
  width: 22px;
  height: 22px;
  overflow: hidden;
}

.burger-open__first-line {
  border: $burger-open-border;
  transform: rotate(135deg);
  position: relative;
  top: 7px;
  left: -8px;
  width: 200%;
}

.burger-open__second-line {
  border: $burger-open-border;
  transform: rotate(-135deg);
  position: relative;
  top: 11px;
  right: 8px;
  width: 200%;
}

.burger-open__list-item-wrapper {
  background-color: $burger-open-list-item-wrapper;
  margin: 0 0 30px 0;
  padding: 0;
  max-height: 60px;
  &:last-child {
    margin: 0;
  }
}

.burger-open__list-item {
  margin: 0;
  padding: 15px;
  position: relative;

  &::after {
    content: "";
    background-image:  url('../img/burger-menu/arrow.svg');
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
    position: absolute;
    top: 20px;
    right: 15px;
  }
}

.burger-open__list-item a {
  @include semibold-fonts();
  font-size: 20px;
  line-height: 28px;
  color: $burger-open-list-item-text;
}

.burger-open__action {
  display: none;
  transform: translateX(100%);
}
