.page-body {
  font-family: "Montserrat Regular", "Arial", "sans-serif";
  font-size: 16px;
  line-height: 28px;
  color: rgba(44, 46, 63, 0.7);
  min-height: 100vh;
  overflow-x: hidden;
  user-select: none;
}

.center-container {
  display: flex;
  flex-direction: column;
  max-width: 1920px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 26px 0 0 0;
  background-color: white;
}
@media (max-width: 1023px) {
  .center-container {
    padding: 0;
  }
}

.page-header {
  min-width: 100%;
  margin: 0 0 32px 0;
  padding: 10px 50px 0 50px;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 0 0 0 21px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding: 0 0 0 12px;
  }
}

.page-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1170px;
  margin: 0 auto;
  transition: 1s all;
}

.page-header__logo-wrapper {
  margin: 0;
}

.page-header__logo {
  display: block;
}

.page-header__img {
  width: 66px;
  display: block;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__img {
    height: 22px;
    width: 57px;
  }
}

.page-header__text-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .page-header__text-block {
    display: none;
  }
}

.page-header__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Montserrat Medium", "Arial", "sans-serif";
  font-size: 24px;
  line-height: 29px;
  color: #2c2e3f;
  font-size: 14px;
  line-height: 17px;
  margin: 0 110px 0 0;
  padding: 0;
}

.page-header__list-item {
  margin: 0 40px 0 0;
  padding: 0;
}
.page-header__list-item:last-child {
  margin: 0;
}

.page-header__list a {
  color: #141517;
}

.page-header__list-item:hover a {
  color: #f17d4b;
}

.page-header__phone {
  font-size: 18px;
  line-height: 32px;
  position: relative;
  color: #141517;
  text-decoration: underline;
}
.page-header__phone::before {
  content: "";
  background: url("../img/header/phone.svg") no-repeat;
  background-size: 22px 25px;
  display: inline-block;
  width: 20px;
  height: 28px;
  position: absolute;
  top: 0;
  left: -35px;
}

.page-header__phone:hover {
  color: #f17d4b;
}
.page-header__phone:hover::before {
  content: "";
  background: url("../img/header/phone-hover.svg") no-repeat;
  background-size: 22px 25px;
  display: inline-block;
  width: 20px;
  height: 28px;
  position: absolute;
  top: 0;
  left: -35px;
}

.page-main {
  flex-grow: 1;
}

.page-footer {
  margin: 0 0 74px 0;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .page-footer {
    padding: 0 21px;
  }
}
@media (max-width: 767px) {
  .page-footer {
    padding: 0 16px;
  }
}

.page-footer__group {
  display: flex;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-footer__group {
    flex-wrap: wrap;
    row-gap: 16px;
  }
}
@media (max-width: 767px) {
  .page-footer__group {
    row-gap: unset;
  }
}

.travelling {
  margin: 0 0 200px 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .travelling {
    margin: 0 0 80px 0;
  }
}
@media (max-width: 767px) {
  .travelling {
    margin: 0 0 83px 0;
  }
}

.travelling__background {
  background-image: url("../img/background/travelling.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-width: 100%;
  min-height: 860px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 187px 0 0 0;
}
@media (max-width: 1023px) {
  .travelling__background {
    min-height: 344px;
    background-image: url("../img/background/travelling-tablet.jpg");
    display: block;
    padding: 61px 0 0 0;
  }
}
@media (max-width: 767px) {
  .travelling__background {
    background-image: url("../img/background/travelling-mobile.jpg");
    min-height: 235px;
    margin: 0px 0 20px 0;
    padding: 83px 16px 0 20px;
  }
}

.travelling__inner {
  max-width: 400px;
  margin-right: 445px;
}
@media (max-width: 1023px) {
  .travelling__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 420px;
    margin: 0 auto;
  }
}

.travelling__title {
  font-family: "Montserrat Semibold", "Arial", "sans-serif";
  font-size: 55px;
  line-height: 80px;
  color: #141517;
  letter-spacing: -1.1px;
  text-align: left;
  color: white;
  margin: 0 0 26px 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .travelling__title {
    font-size: 32px;
    line-height: 45px;
    margin: 0 0 15px 0;
  }
}
@media (max-width: 767px) {
  .travelling__title {
    text-align: center;
    font-size: 26px;
    line-height: 36px;
  }
}

.travelling__text {
  color: white;
  letter-spacing: -0.47px;
  max-width: 85%;
  margin: 0 0 30px 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .travelling__text {
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    margin: 0 0 8px 0;
    max-width: 95%;
  }
}
@media (max-width: 767px) {
  .travelling__text {
    display: none;
  }
}

@media (max-width: 1023px) {
  .travelling__button.button {
    min-height: 50px;
    min-width: 250px;
  }
}
@media (max-width: 767px) {
  .travelling__button.button {
    display: none;
  }
}

.travelling__button-arrow::after {
  content: "";
  background: url("../img/travelling/arrow.svg") no-repeat;
  width: 17px;
  height: 17px;
  display: inline-block;
  padding: 0;
}
@media (max-width: 1023px) {
  .travelling__button-arrow::after {
    background: url("../img/travelling/arrow-tablet.svg") no-repeat;
  }
}

.travelling__text-mobile {
  display: none;
}
@media (max-width: 767px) {
  .travelling__text-mobile {
    display: block;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .travelling__text-mobile {
    letter-spacing: -0.46px;
    line-height: 27px;
    margin: 0 0 19px 0;
    padding: 0 16px;
  }
}

.travelling__button-mobile.button {
  display: none;
  letter-spacing: 2px;
  padding-left: 10px;
}
@media (max-width: 1023px) {
  .travelling__button-mobile.button {
    font-size: 10px;
    line-height: 12px;
  }
}
@media (max-width: 767px) {
  .travelling__button-mobile.button {
    display: flex;
    min-width: 288px;
    min-height: 50px;
    margin: 0 auto;
  }
}

@media (max-width: 1023px) {
  .travelling__button-text {
    padding-left: 10px;
  }
}
@media (max-width: 767px) {
  .travelling__button-text {
    padding: 5px 0 0 30px;
  }
}

.travelling__button-arrow {
  padding: 7px 9px 0 0;
}

.button {
  font-family: "Montserrat Bold", "Arial", "sans-serif";
  font-size: 25px;
  line-height: 34px;
  color: rgba(20, 21, 23, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 15px;
  min-width: 340px;
  min-height: 75px;
  text-transform: uppercase;
  background-color: #fe7865;
  border: 0;
  color: white;
  letter-spacing: 2px;
  margin: 0;
  padding: 10px 30px;
  transition: 0.2s;
}
@media (max-width: 1023px) {
  .button {
    font-family: "Montserrat Medium", "Arial", "sans-serif";
    font-size: 10px;
    line-height: 12px;
  }
}

.button:hover {
  background-color: #f17d4b;
  transition: 0.2s;
}

.places {
  margin: 0 0 116px 0;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .places {
    margin: 0 0 81px;
    padding: 0 21px;
  }
}
@media (max-width: 767px) {
  .places {
    padding: 0 16px;
    margin: 0;
  }
}

.places__inner {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 1270px) {
  .places__inner {
    max-width: 760px;
  }
}

.places__title {
  font-family: "Montserrat Semibold", "Arial", "sans-serif";
  font-size: 55px;
  line-height: 80px;
  color: #141517;
  margin: 0;
  padding: 0;
  letter-spacing: -1.245px;
  max-width: 358px;
}
@media (max-width: 1023px) {
  .places__title {
    font-size: 32px;
    max-width: none;
    text-align: center;
    margin: 0 auto 20px auto;
    line-height: 45px;
  }
}
@media (min-width: 1024px) {
  .places__title::after {
    content: "";
    border-bottom: 4px solid #fe7865;
    display: block;
    width: 229px;
    padding: 30px 0 0 0;
    margin: 0 0 30px 0;
  }
}
@media (max-width: 767px) {
  .places__title {
    font-size: 26px;
    line-height: 36px;
  }
}

.places__text {
  max-width: 340px;
  letter-spacing: -0.47px;
  margin: 0 0 55px 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .places__text {
    text-align: center;
    letter-spacing: -0.46px;
    margin: 0 auto 40px auto;
  }
}
@media (max-width: 767px) {
  .places__text {
    margin-bottom: 18px;
  }
}

.places__img-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 60px;
  row-gap: 90px;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .places__img-wrapper {
    row-gap: 32px;
    column-gap: 40px;
  }
}
@media (max-width: 767px) {
  .places__img-wrapper {
    row-gap: 0;
    column-gap: 0;
  }
}

.places__img-card {
  position: relative;
  display: flex;
}
@media (max-width: 767px) {
  .places__img-card {
    margin-bottom: 26px;
  }
}

.places__img-card:hover::before {
  content: "";
  margin: auto;
  padding: 0;
  background-color: rgba(20, 21, 23, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.places__img-card:hover .places__white-button {
  display: block;
  display: flex;
  justify-content: center;
  align-items: center;
}

.places__country-wrapper {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 9px;
  background-color: white;
  min-width: 215px;
  min-height: 62px;
  text-align: center;
}
@media (max-width: 1023px) {
  .places__country-wrapper {
    min-height: 60px;
  }
}
@media (max-width: 767px) {
  .places__country-wrapper {
    min-width: 214px;
    min-height: 60px;
  }
}

.places__country {
  left: 50%;
  position: absolute;
  top: 44%;
  font-size: 24px;
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .places__country {
    line-height: 29px;
    letter-spacing: -0.41px;
  }
}

@media (max-width: 1023px) {
  .places__img {
    height: 425px;
    width: 288px;
  }
}

.places__white-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 266px;
  min-height: 77px;
  border: 1px solid white;
  z-index: 1;
  display: none;
}

.places__white-button-text {
  font-family: "Montserrat Medium", "Arial", "sans-serif";
  font-size: 24px;
  line-height: 29px;
  color: #2c2e3f;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: white;
}

.places__img-card-anchor {
  display: flex;
  color: #2c2e3f;
}

.reviews {
  margin: 0 auto;
  padding: 0 50px;
  position: relative;
  overflow: hidden;
