}
@media (max-width: 1023px) {
  .reviews__background {
    background-image: none;
    min-height: unset;
  }
}

.reviews__inner {
  position: relative;
  display: flex;
  column-gap: 100px;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 1023px) {
  .reviews__inner {
    display: block;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .reviews__inner {
